import React, { useEffect, useState } from "react";
import "./App.css";
import AddQuestionPopup from "./components/AddQuestionPopup";
import ParticlesBg from "particles-bg";
import QuizCard from "./components/QuizCard";

const MemoizedParticlesBg = React.memo(() => (
  <ParticlesBg type="random" num={25} bg={true} />
));

const App = () => {
  const [questions, setQuestions] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [showPopup, setShowPopup] = useState(false);
  const [showAddButton, setShowAddButton] = useState(false);

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + "/data.json")
      .then((res) => res.json())
      .then((data) => setQuestions(data.questions))
      .catch((err) => console.error('Failed question load', err));
  }, []);

  const fetchQuestion = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % questions.length);
  };

  const handleAddQuestion = (formData) => {
    const newQuestion = {
      id: Date.now(),
      question: formData.question,
      option_1: formData.option_1,
      option_2: formData.option_2,
      option_3: formData.option_3,
      option_4: formData.option_4,
      correct_option: formData.correct_option
    };

    const updatedQuestions = [...questions, newQuestion];
    setQuestions(updatedQuestions);
    localStorage.setItem("questions", JSON.stringify(updatedQuestions));

    setShowPopup(false);
  };

  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.shiftKey && e.key === "Q") {
        setShowAddButton((prevState) => !prevState);
      }
    };
    window.addEventListener("keydown", handleKeyDown);
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, []);

  return (
    <>
      <div className="app">
        <div className="quiz-container">
          {questions.length > 0 && (
            <QuizCard question={questions[currentIndex]} onNext={fetchQuestion} />
          )}
          {showAddButton && (
            <button className="add-question-btn" onClick={() => setShowPopup(true)}>
              Add Question
            </button>
          )}
        </div>
        {showPopup && (
          <AddQuestionPopup onClose={() => setShowPopup(false)} onSubmit={handleAddQuestion} />
        )}
      </div>
      <MemoizedParticlesBg />
    </>
  );
};

export default App;
